const $siteList = $(".siteList");
const $lastLi = $siteList.find("li.last"); //æ‰¾åˆ°æ–°å¢žç½‘ç«™ä¹‹å‰çš„æœ€åŽä¸€ä¸ª li
const x = localStorage.getItem("x"); //è¯»å‡º x
const xObject = JSON.parse(x); //æŠŠxå˜æˆå¯¹è±¡

const hashMap = xObject || [
  //å¦‚æžœxObjectå­˜åœ¨ï¼Œå°±ç”¨xObjectï¼Œä¸å­˜åœ¨å°±ç”¨å†™çš„é»˜è®¤çš„å±žæ€§
  { logo: "B", url: "https://www.bilibili.com" },
  { logo: "G", url: "https://www.google.com" },
];

const simplifyUrl = (url) => {
  return url
    .replace("https://", "")
    .replace("http://", "")
    .replace("www.", "")
    .replace(/\/.*/, ""); // .*æ˜¯/åŽé¢çš„ä»»ä½•ä¸œè¥¿ï¼Œ åˆ é™¤ / å¼€å¤´çš„å†…å®¹
};

const render = () => {
  $siteList.find("li:not(.last)").remove();
  hashMap.forEach((node, index) => {
    const $li = $(
      `<li>
          <div class="site">
            <div class="logo">${node.logo}</div>
            <div class="link">${simplifyUrl(node.url)}</div>
            <div class="close">
              <svg class="icon">
                <use xlink:href="#icon-close"></use>
              </svg>
            </div>
          </div>
    </li>`
    ).insertBefore($lastLi);
    $li.on("click", () => {
      //ç‚¹å‡»æ¯ä¸€ä¸ªå¯¼èˆªçš„æ—¶å€™å®žçŽ°è·³è½¬
      window.open(node.url);
    });
    $li.on("click", ".close", (e) => {
      e.stopPropagation(); //é˜»æ­¢å†’æ³¡ï¼Œç‚¹å‡»å…³é—­å›¾æ ‡ä¸è·³è½¬
      hashMap.splice(index, 1); //åˆ é™¤
      render(); //åˆ é™¤ä¹‹åŽï¼Œé‡æ–°æ¸²æŸ“ hashMap
    });
  });
};

render();

$(".addButton").on("click", () => {
  let url = window.prompt("è¯·é—®ä½ è¦æ·»åŠ çš„ç½‘å€æ˜¯ä»€ä¹ˆï¼Ÿ"); //å¾—åˆ°ç”¨æˆ·è¾“å…¥çš„ç½‘å€
  if (url.indexOf("http") !== 0) {
    //å¦‚æžœç”¨æˆ·è¾“å…¥çš„åœ°å€ä¸æ˜¯httpå¼€å¤´çš„
    url = "https://" + url;
  }
  hashMap.push({
    logo: simplifyUrl(url)[0].toUpperCase(), //ç®€åŒ–ç½‘å€ï¼Œæ–°å¢žç½‘ç«™åŽæ˜¾ç¤ºç®€åŒ–çš„åŽçš„ç¬¬ä¸€ä¸ªå¤§å†™å­—æ¯
    url: url,
  });
  render();
});

//å…³é—­é¡µé¢çš„æ—¶å€™æŠŠå½“å‰çš„ hashMap å¯¹è±¡å˜æˆå­—ç¬¦ä¸² å­˜åˆ° x é‡Œé¢
window.onbeforeunload = () => {
  const string = JSON.stringify(hashMap);
  localStorage.setItem("x", string);
};

$(document).on("keypress", (e) => {
  const { key } = e; //ç­‰ä»·äºŽconst key = e.key
  for (let i = 0; i < hashMap.length; i++) {
    if (hashMap[i].logo.toLowerCase() === key) {
      window.open(hashMap[i].url);
    }
  }
});
